body {
  text-align: center;
  background-color: rgba(182, 177, 177, .965);
  padding: 24px;
  font-family: Lato, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

.search-form {
  z-index: 1;
  width: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

input, button {
  height: 48px;
  border-radius: 12px;
  margin: 12px;
  padding: 8px;
  font-family: inherit;
  font-size: 18px;
}

input {
  width: 226px;
  background-color: rgba(231, 196, 220, .604);
  border-color: #1ac584;
  outline: none;
}

input::-ms-input-placeholder {
  color: rgba(31, 30, 30, .52);
}

input::placeholder {
  color: rgba(31, 30, 30, .52);
}

button {
  width: 124px;
  height: 68px;
  cursor: pointer;
  color: #fff;
  font-style: inherit;
  background-color: #0ddb8c;
  border-color: rgba(0, 0, 0, 0);
  outline: none;
}

input:hover, input:focus, input:active {
  border-color: #0cf099;
}

button:hover, button:focus, button:active {
  background-color: #1ac584;
}

.load-more {
  height: 48px;
}

.load-more.hidden, .end-collection.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding: 60px 0;
  display: flex;
}

.photo-card {
  width: 340px;
  height: 100%;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.info {
  color: rgba(31, 30, 30, .52);
  margin-top: 8px;
  display: flex;
}

.info-item {
  font-size: 16px;
  font-weight: 700;
}

.info-item:not(:last-child) {
  margin-right: 6px;
}

.end-collection {
  color: rgba(31, 30, 30, .52);
  font-size: 16px;
  font-weight: 700;
}

